<template>
  <div class="chart-container">
      from father msg:{{msg}}
      <el-button @click="increase">increase</el-button>
      count:{{count}}
  </div>
  
</template>

<script>
export default {
  name: 'ChartMix',
  props: {
    msg: String
  },
  created(){
    
  },
  computed:{
    count(){
      return this.$store.state.chartSetting.count;
    }
  },
  methods:{
    increase(){
      this.$store.commit('increment')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chart-container{
  background: rgb(236, 50, 4);
}
</style>
