// use vuex modules

//chart setting
const chartSetting = {
    state: () => ({
        count: 0
      }),
    getters: {
        doubleCount (state) {
            return state.count * 2
          }
    },
    mutations: {
        increment (state) {
            //  `state` 
            state.count++
          }
    },
    actions: {

    }
}

export { chartSetting }